body {
  background-image: linear-gradient(to bottom, #f1f2f6, #dfe4ea, #ced6e0);
  font-family: "Montserrat", sans-serif;
  color: #555;
  overflow: hidden;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}

.container-main {
  display: grid;
  grid-template-columns: 1fr 1fr 6fr;
  grid-template-areas: "header aside main";
  height: 100vh;
}

header {
  display: grid;
  align-items: center;
  justify-content: flex-end;
  grid-area: header;
}

main {
  display: grid;
  align-items: center;
  grid-area: main;
}

aside {
  display: grid;
  align-items: center;
  justify-content: center;
  grid-area: aside;
}

/*MENU*/
.menu {
  background: #fff;
  border-radius: 10px 0 0 10px;
}
.menu ul {
  list-style: none;
  line-height: 3rem;
  text-align: left;
}
.menu ul li {
  border-bottom: 1px solid #ccc;
  transition: all linear 1s;
}

.menu ul li:hover {
  transition: all linear 1s;
  border-bottom: 1px solid #e4e450;
}
.menu ul li a {
  text-decoration: none;
  color: #ccc;
  transition: all linear 1s;
}
.menu ul li a:hover {
  transition: all linear 1s;
  color: #e4e450;
}
/*CARD PAGE*/
.card-shadow {
  box-shadow: 10px 0 10px 0 #eee;
  z-index: 999;
}

.card-page {
  display: grid;
  background-color: #fff;
  padding: 4rem;
  height: 60vh;
}

.card-page,
article header {
  display: grid;
  align-items: center;
  justify-content: flex-start;
}
.card-page,
article {
  display: grid;
  align-items: center;
  justify-content: flex-start;
  line-height: 2rem;
}

.title-page {
  border-left: solid 2px #eee;
  padding-left: 0.2em;
  margin-bottom: 1rem;
}

/*PROFILE*/
.profile {
  padding: 2rem;
  background: #fff;
  display: grid;
  align-items: center;
  justify-content: center;
  border: solid 1px #ccc;
}
.profile img {
  width: 250px;
  border-radius: 50%;
}

.description {
  display: grid;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.social {
  display: grid;
  align-items: center;
  justify-content: center;
}
.social,
ul {
  text-align: center;
  list-style: none;
  line-height: 2rem;
}

.social,
li a {
  color: #666;
  padding: 0 1rem;
  text-align: center;
  transition: all linear 1s;
}

.social,
li a:hover {
  color: #e4e450;
  padding: 0 1rem;
  text-align: center;
  transition: all linear 1s;
}

.btn {
  display: grid;
  align-items: center;
  justify-content: center;
}
.btn-download {
  padding: 1rem;
  background: #e4e450;
  font-weight: 600;
  color: #6f6f13;
  border-radius: 10px;
  transition: all linear 0.4s;
}

.btn-download:hover {
  transition: all linear 0.4s;
  background: #cece27;
  color: #6f6f13;
}

.scroll {
  height: 500px;
  overflow-y: scroll;
}
.list-group {
  display: grid;
  justify-content: flex-start;
}
.list-title {
  font-weight: 600;
  color: #cece27;
  text-align: left;
}
.list-group-item {
  text-align: left;
}
.list-of-skills {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: baseline;
}
.card-body {
  border: solid 1px #ccc;
  padding: 1rem;
}
.card-title {
  font-weight: 600;
  color: #cece27;
  font-size: 1rem;
}
.card-subtitle {
  font-weight: 400;
  font-size: 1.2rem;
}
.card-link {
  font-size: 0.8rem;
}
